* {
  box-sizing: border-box;
}
.nav {
  background-color: rgb(226, 224, 224);
  padding-top: 50px;
  padding-bottom: 5px;
}

.nav ul{
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
  /* justify-content: space-evenly; */
  justify-content: end;
  padding: 0px 120px 5px 0;
  margin: 0;
}
.nav a{
  text-decoration: none;
  color: inherit;
  display: flex;
}
.nav a:hover {
  text-decoration: underline;
  color: green;
}
.data-card {
  padding-top: 70px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: space-between;
}
.card {
  border: 2px solid #e6e4e4;
  border-radius: 8px;
  overflow: hidden;
  margin: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  width: 330px;
  display: flex;
  flex-direction: column;
  /* height: 100% !important; */
  /* padding-bottom: 20px; */
}
.poster{
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 1px solid #ccc;
}
.name {
  text-align: justify;
  font-size: 1.2rem;
  margin: 0;
}
.creates {
  font-size: 0.9rem;
  color: #555;
}
.time {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 0.8rem;
  color: #777;
}
